import React, { useState } from 'react';

function ItemCounter() {
    const [count, setCount] = useState(0);

    const increment = () => {
        setCount(count + 1);
    };

    const decrement = () => {
        if (count > 0) {
            setCount(count - 1);
        }
    };

    return (
        <div className='flex flex-row gap-2 items-center rounded-2xl bg-gray-50 border'>
            <button className='text-xl rounded-l-2xl py-1 px-3 border bg-gray-100 shadow-md shadow-gray-400 transition duration-300 hover:shadow-none' onClick={decrement}>-</button>
            <span className='p-2'>{count}</span>
            <button className='text-xl rounded-r-2xl py-1 px-3 border bg-gray-100 shadow-md shadow-gray-400 transition duration-300 hover:shadow-none' onClick={increment}>+</button>
        </div>
    );
}

export default ItemCounter;
